/* @mixin key-titleLine { @keyframes titleLine { 0% {height: 0;} 100%{height: 5.5em;} } }*/
@keyframes bounceOutline { 0% { box-shadow: 0 0 0 4px #ffe079; }
		25% { box-shadow: 0 0 0 2px #ffe079; }
		50% { box-shadow: 0 0 0 6px #ffe079; }
		75% { box-shadow: 0 0 0 4px #ffe079; }
		100% { box-shadow: 0 0 0 5px #ffe079; } }

@-webkit-keyframes bounceOutline { 0% { box-shadow: 0 0 0 4px #ffe079; }
		25% { box-shadow: 0 0 0 2px #ffe079; }
		50% { box-shadow: 0 0 0 6px #ffe079; }
		75% { box-shadow: 0 0 0 4px #ffe079; }
		100% { box-shadow: 0 0 0 5px #ffe079; } }

@keyframes moveToRight { 3% { margin-left: -2px; }
		50% { margin-left: 6px; }
		100% { margin-left: -2px; } }

@keyframes visualFog01 { 0% { right: 0%; }
		100% { right: 30%; } }

@keyframes visualFog02 { 0% { left: 0%; }
		100% { left: 10%; } }

/*  
*   RESET STYLE
*/
html, body, div, span, h1, h2, h3, h4, h5, h6, p, em, img, small article, aside, footer, header, nav, address, section, main, time, ol, ul, li, hr, strong, figure { margin: 0; padding: 0; border: 0; vertical-align: baseline; -webkit-text-size-adjust: none; font-style: normal; }

ul, ol, li, dl { list-style: none; }

a:link, a:visited, a:hover, a:active { text-decoration: none; font-family: 'Lato', 'LotteMartDream',  AppleGothic, sans-serif, Helvetica; }

hr { display: none; }

button { cursor: pointer; font-family: 'Lato', 'LotteMartDream',  AppleGothic, sans-serif, Helvetica; }

input { margin: 0; padding: 0; box-sizing: content-box; vertical-align: top; border-radius: 0; -webkit-border-radius: 1px; -webkit-apprearance: none; }

html, body { position: relative; overflow-x: hidden; width: 100%; min-width: 100%; font-size: 100%; font-family: 'Lato', 'LotteMartDream',  AppleGothic, sans-serif, Helvetica; line-height: 1.3; }

/*** html, body에 overflow:hidden + height: 100%를 지정하면 버벅거림이 없어진다는 얘기가 있는데.....height:100%를 하면 사파리에서 푸터가 안나온다 ㅠㅠ 어쩌죠..? ***/
/*  
*   COMMON STYLE
*/
.no_scroll { overflow: hidden; }

.text_indent { text-indent: -9999px; }

.text_hide { position: absolute; left: -999em; }

.clearfix { *zoom: 1; }

.clearfix:after { display: block; content: ''; clear: both; }

.ly_mt_sm { margin-top: 1em; }

.br_m { display: none; }

.br_pc { display: block; }

@media screen and (max-width: 640px) { .br_m { display: block !important; }
		.br_pc { display: none !important; }
		.text_hide_m { display: none !important; } }

.wrap { z-index: 1; position: relative; background-color: #fafafa; }

.inner { position: relative; }

.btn_effect:before { display: block; position: absolute; top: 0; left: 0; bottom: 0; right: 0; margin: auto; width: 100%; height: 1px; content: '.'; color: transparent; background-color: #fff; opacity: 0; transition: height .5s, opacity .5s; }

.btn_effect:hover span { color: #222; transition: color .5s; }

.btn_effect:hover:before { opacity: 1; height: 100%; transition: height .5s, opacity .5s; }

.btn_effect:hover:after { background-image: url(../images/ico_link_dark.svg) !important; transition: background-image .5s; }

.ani_elem { opacity: 0; }

.item_link { cursor: default; }

