/* title for section, aricle */
.title_line { position: absolute; top: -3.5em; left: 50%; display: block; content: ''; width: 1px; height: 5.5em; background-color: #e3e1e1; transition: background-color 2s; }

.title_line.on { background-color: #67ceeb; }

.title { display: table; position: relative; width: auto; color: #8cddeb; z-index: 1; margin: 0 auto; padding-top: 0.5em; font-size: 60px; font-weight: 700; text-align: center; transition: font-size .5s; /*  &.on { top: -10px; }*/ }

.title[data-ani] { color: #e3e1e1; }

.title:before { overflow: hidden; position: absolute; width: 0; color: #8cddeb; content: attr(data-ani); transition: width .55s; }

.title.on:before { width: 100%; text-align: center; transition: width .55s; }

.title_sm { display: block; position: relative; z-index: 1; font-style: normal; font-size: 11px; letter-spacing: 1px; text-align: center; color: #aaa; font-weight: 300; text-transform: uppercase; transition: font-size .5s, opacity .5s; }

.title_sm[data-ani] { opacity: 0; }

.title_sm.on { opacity: 1; transition: opacity .5s; }

