/* fixed (logo, menu) */
.header { z-index: 300; width: 100%; position: fixed; top: 0; left: 0; }

.header .fixed { position: relative; width: 100%; height: 40px; max-width: 1920px; margin: 0 auto; }

.header .fixed .logo { cursor: pointer; opacity: 1; z-index: 300; position: absolute; top: 20px; left: 16px; width: 40px; height: 40px; transition: width .5s, height .5s, top .5s, left .5s, opacity .3s; }

.header .fixed .logo.onTop { opacity: 0; }

.header .fixed .menu { z-index: 600; position: absolute; width: auto; height: auto; top: 46px; right: 36px; cursor: pointer; }

.header .fixed .menu .menu_burger { position: absolute; width: 32px; height: 20px; top: 50%; left: 50%; transform: translate(-50%, -50%); transition: transform 750ms ease-in-out, width .5s, height .5s, top .5s, left .5s; }

.header .fixed .menu .menu_burger:focus { outline: 0; }

.header .fixed .menu .menu_burger .line { position: absolute; right: 0; background-color: #777; height: 2px; width: 80%; pointer-events: none; transition: transform .35s ease-in-out, background-color .2s; }

.header .fixed .menu .menu_burger .line_01 { top: 0; }

.header .fixed .menu .menu_burger .line_02 { width: 100%; top: 0; bottom: 0; margin: auto; }

.header .fixed .menu .menu_burger .line_02:before { content: ''; position: absolute; width: 100%; height: 2px; background-color: #777; transition: transform .35s ease-in-out, background-color .2s; }

.header .fixed .menu .menu_burger .line_03 { bottom: 0; }

.header .fixed .menu.onTop .line, .header .fixed .menu.onTop .line_02:before { background-color: rgba(242, 234, 234, 0.9); }

.header .fixed .menu.open .menu_burger .line_01 { opacity: 0; transform: translate3d(-18px, 0, 0); transition: all .35s ease-in-out; }

.header .fixed .menu.open .menu_burger .line_02 { transform: rotate(45deg); transition: transform .35s ease-in-out; }

.header .fixed .menu.open .menu_burger .line_02:before { transform: rotate(-90deg); transition: transform .35s ease-in-out; }

.header .fixed .menu.open .menu_burger .line_03 { opacity: 0; transform: translate3d(18px, 0, 0); transition: all .35s ease-in-out; }

.header .fixed .menu.open .menu_burger:focus { outline: 0; }

@media screen and (min-width: 1601px) { .header .fixed .logo { left: 18px; width: 46px; height: 46px; }
		.header .fixed .menu { top: 42px; right: 36px; }
		.header .fixed .menu .menu_burger { width: 38px; height: 24px; } }

@media screen and (min-width: 1201px) and (max-width: 1600px) { .header .fixed .logo { width: 42px; height: 42px; }
		.header .fixed .menu { top: 38px; right: 38px; }
		.header .fixed .menu .menu_burger { width: 36px; height: 22px; } }

@media screen and (min-width: 641px) and (max-width: 1200px) { .header .fixed .logo { left: 10px; }
		.header .fixed .menu { top: 42px; right: 26px; }
		.header .fixed .menu .menu_burger { width: 30px; height: 20px; } }

@media screen and (max-width: 640px) { .header .fixed .logo { top: 14px; left: 14px; width: 30px; height: 30px; }
		.header .fixed .menu { top: 30px; right: 28px; }
		.header .fixed .menu .menu_burger { width: 26px; height: 18px; right: 10px; } }

/* gnb */
.gnb.open { visibility: visible; opacity: 1; bottom: 0; transition: all 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86); }

.gnb { visibility: hidden; opacity: 0; z-index: 300; position: fixed; overflow: hidden; width: 100%; height: auto; top: 0; left: 0; right: 0; bottom: 100%; text-align: center; transition: all 0.45s cubic-bezier(0.785, 0.135, 0.15, 0.86); background-color: #f2f2f2; }

.gnb .nav { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; justify-content: center; align-items: center; height: 100%; min-height: 390px; margin-top: -2em; }

.gnb .nav .menu_item { position: relative; text-align: center; margin-top: 3%; }

.gnb .nav .menu_item a { display: block; color: #c5c3c0; font-family: 'Lato'; font-weight: 700; text-transform: uppercase; }

.gnb .nav .menu_item:after { opacity: 0; display: block; margin-top: -4px; letter-spacing: 1px; color: #cccccc; font-weight: 300; text-transform: uppercase; transition: opacity .3s; }

.gnb .nav .menu_item:hover a { color: #8cddeb; }

.gnb .nav .menu_item:first-child { margin-top: 0; }

.gnb .nav .menu_item:first-child:after { content: 'Go to Home'; }

.gnb .nav .menu_item:nth-child(2):after { content: 'See my work'; }

.gnb .nav .menu_item:nth-child(3):after { content: 'Know me'; }

.gnb .nav .menu_item:hover:after { opacity: 1; transition: opacity .3s; }

.gnb .contact { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: row; justify-content: center; position: absolute; bottom: 2em; left: 50%; width: 100%; transform: translateX(-50%); }

.gnb .contact .contact_link { text-align: center; margin-left: 5em; line-height: 180%; }

.gnb .contact .contact_link:first-child { margin-left: 0; }

.gnb .contact .contact_link .contact_heading { display: inline-block; text-align: center; width: initial; margin-right: 0.8em; font-weight: bold; color: #4c4c4c; font-size: 100%; }

.gnb .contact .contact_link .contact_info { display: inline-block; color: #bbbbbb; font-size: 100%; }

.gnb .contact .contact_link:hover .contact_info { color: #ffde7d; }

@media screen and (min-width: 1921px) { .gnb .nav .menu_item a { font-size: 70px; }
		.gnb .nav .menu_item:after { font-size: 14px; }
		.gnb .contact .contact_link { font-size: 19px; } }

@media screen and (min-width: 1601px) { .gnb .nav .menu_item a { font-size: 76px; }
		.gnb .nav .menu_item:after { font-size: 15px; }
		.gnb .contact .contact_link { font-size: 20px; } }

@media screen and (min-width: 1201px) and (max-width: 1600px) { .gnb .nav .menu_item a { font-size: 64px; }
		.gnb .nav .menu_item:after { font-size: 13px; }
		.gnb .contact .contact_link { font-size: 17px; } }

@media screen and (min-width: 641px) and (max-width: 1200px) { .gnb .nav .menu_item a { font-size: 62px; }
		.gnb .nav .menu_item:after { font-size: 12px; }
		.gnb .contact .contact_link { font-size: 15px; margin-left: 3em; } }

@media screen and (max-width: 640px) { .gnb .nav .menu_item { margin-top: 1.5em; }
		.gnb .nav .menu_item a { font-size: 44px; }
		.gnb .nav .menu_item:after { font-size: 11px; }
		.gnb .contact { flex-direction: column; }
		.gnb .contact .contact_link { display: table; width: 230px; margin: 4px auto 0; text-align: left; font-size: 14px; line-height: 140%; }
		.gnb .contact .contact_link:first-child { margin: 0 auto; }
		.gnb .contact .contact_link .contact_heading { text-align: right; display: inline-block; width: 70px; margin-right: 0.8em; }
		.gnb .contact .contact_link .contact_info { display: inline-block; } }

@media screen and (max-width: 375px) { .gnb .contact .contact_link { width: 200px; font-size: 13px; }
		.gnb .contact .contact_link .contact_heading { width: 60px; margin-right: 0.6em; } }

/* visual */
.visual { overflow: hidden; position: relative; width: 100%; max-width: 2560px; height: 100vh; min-height: 740px; margin: 0 auto; background-image: url(../images/visual_blue_1920.png); background-position: 50% 80%; background-repeat: no-repeat; background-size: cover; }

.visual .inner { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; justify-content: center; align-items: center; position: relative; width: 100%; height: 100%; background-color: rgba(36, 33, 33, 0.46); }

.visual .visual_title { margin-top: -1.5em; text-align: center; text-transform: uppercase; }

.visual .visual_title .title_text { position: relative; left: 10px; font-family: 'Lato', AppleGothic, sans-serif, Helvetica; font-weight: 300; font-size: 40px; color: rgba(255, 255, 255, 0.8); }

.visual .visual_title .title_text i { position: relative; opacity: 0; font-style: normal; text-shadow: 0 0 4px #fff, 0 0 2px rgba(255, 245, 217, 0.6); }

.visual .visual_title .title_text > span { position: relative; opacity: 0; }

.visual .visual_title .title_logo { opacity: 0; display: block; width: 240px; height: 240px; margin: 0.5em auto 1.5em; text-align: center; }

.visual .visual_elem { opacity: 1; position: absolute; }

.visual .visual_tree01 { left: 0; width: 22%; height: 100%; max-width: 360px; background-image: url(../images/visual_elem_1.png); background-size: 100%; background-repeat: no-repeat; background-position: -5% bottom; }

.visual .visual_tree02 { left: 0; width: 20%; max-width: 280px; height: 100%; background-image: url(../images/visual_elem_2.png); background-size: 100%; background-repeat: no-repeat; background-position: -10% bottom; }

.visual .visual_tree03 { right: 0; width: 22%; max-width: 300px; height: 100%; background-image: url(../images/visual_elem_3.png); background-size: 100%; background-repeat: no-repeat; background-position: 0% bottom; }

.visual .visual_tree04 { right: 0; width: 20%; max-width: 250px; height: 100%; background-image: url(../images/visual_elem_4.png); background-size: 100%; background-repeat: no-repeat; background-position: 0 bottom; }

.visual .visual_fog01 { animation: visualFog01 28s ease-in-out infinite alternate; right: 0%; opacity: 0.6; width: 70%; height: 100%; background-image: url(../images/visual_elem_5.png); background-size: 90%; background-repeat: no-repeat; background-position: 0 100%; }

.visual .visual_fog02 { animation: visualFog02 18s linear infinite alternate; bottom: 15%; left: 0; opacity: 0.25; width: 50%; height: 50%; background-image: url(../images/visual_elem_6.png); background-size: 100%; background-repeat: no-repeat; background-position: 50% 50%; }

.visual .scroll_text { position: relative; margin-left: -6px; font-family: 'Lato', AppleGothic, sans-serif, Helvetica; font-weight: 300; color: rgba(255, 255, 255, 0.45); font-size: 12px; letter-spacing: 0.12rem; text-transform: uppercase; }

.visual .scroll_text:before, .visual .scroll_text:after { display: block; position: absolute; top: 0.29rem; right: -14px; font-size: 10px; font-family: serif; content: '>'; transform: rotate(90deg); }

.visual .scroll_text:after { top: -0.03rem; }

/* footer */
.footer.on { opacity: 1; }

.footer { position: fixed; z-index: 0; left: 0; right: 0; bottom: 0; width: 100%; height: auto; font-size: 16px; box-sizing: border-box; }

.footer .footer_title { padding-top: 1em; }

.footer .footer_text { margin: 0.5em 0 0; color: #bfbfbf; text-align: center; }

.footer .footer_address { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; justify-content: center; margin-top: 3em; padding-bottom: 4.5em; border-bottom: 1px solid #ddd; }

.footer .footer_address .footer_link { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; font-family: 'NotoSans', AppleGothic, sans-serif, Helvetica; position: relative; font-size: 20px; color: #484848; letter-spacing: 0.08rem; align-items: center; transition: color .2s ease-in-out; }

.footer .footer_address .footer_link .icon_circle { position: relative; display: block; width: 3.31em; height: 3.31em; margin-left: 0.8em; line-height: 3.31em; border-radius: 50%; background-color: #ffe079; box-shadow: 0 0 0 5px #ffe079; transition: width .5s, height .5s; font-size: 80%; margin-right: 1em; }

.footer .footer_address .footer_link .icon_circle:after { z-index: -1; display: block; position: absolute; top: 0; left: 0; width: 100%; height: 100%; border-radius: 50%; box-shadow: 0 0.5em 0.938em #ccc; content: ''; }

.footer .footer_address .footer_link .icon_circle:before { display: block; position: absolute; top: 50%; left: 50%; width: 54%; height: 54%; transform: translateX(-50%) translateY(-50%); }

.footer .footer_address .footer_link:hover { color: #ffcd47; transition: color .2s ease-in-out; }

.footer .footer_address .footer_link:hover .icon_circle { cursor: pointer; animation: bounceOutline 0.55s ease-in-out 1; }

.footer .footer_address .link_mail .icon_circle:before { background-image: url(../images/ico_mail.svg); background-size: cover; content: ''; }

.footer .footer_address .link_mail .footer_link_text:after { display: block; content: 'nykim@nykim.net'; }

.footer .footer_address .link_phone { margin-left: 2em; text-align: justify; }

.footer .footer_address .link_phone .icon_circle:before { background-image: url(../images/ico_phone.svg); background-size: cover; content: ''; }

.footer .footer_address .link_phone .footer_link_text:after { display: block; content: '010 - 2728 - 5936'; }

.footer .footer_bottom { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; justify-content: space-between; align-items: center; max-width: 1920px; margin: 0 auto; padding: 0.6em 1em; }

.footer .footer_bottom .social { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; }

.footer .footer_bottom .social .social_item { overflow: hidden; width: 1.6em; height: 1.6em; font-size: 14px; text-indent: -999em; margin-left: 1.3em; }

